.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.user {
  margin-top: 5rem;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: bold;
}

.dice {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
}

.goStone {
  margin-top: 2rem;
}

.score {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
  text-transform: uppercase;
}

.scoreValue {
  font-size: 3rem;
}

.duration {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  margin-top: 3rem;
}

.durationValue {
  font-size: 2rem;
}

.debugStatus {
  margin-top: 3rem;
  text-transform: uppercase;
  color: lightgreen;
  text-align: center;
}
