.container {
    display: flex;
    flex-direction: column;
    border: 2px solid black;
}

.backgroundWoodField {
    display: flex;
    flex-direction: column;
    border: 2px solid black;
    background-image: url('../../images/desc.jpeg');
    background-size: cover;
}

.backgroundDefaultField {
    display: flex;
    flex-direction: column;
    border: 2px solid black;
    background-image: url('../../images/board.png');
    background-size: cover;
}

.fieldRow {
    display: flex;
    flex-direction: row;
}


.fieldCell {
    width: 13rem;
    height: 12rem;
    border: 1px solid black;
}


